.details {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
    }
  }
  &__button {
    @include pageGrid;

    &--explore {
      grid-column: 1 / -1;
      @include onTablet {
        grid-column: 2 / 6;
      }
      @include onDesktop {
        grid-column: 5 / 9;
      }
    }
  }
}
