
/*#region footer */
.footer {
  width: 100%;
  height: 200px;
  @include onTablet {
    height: 280px;
  }
  @include onDesktop {
    height: 555px;
  }

  background-image: url(../images/footer-bg.png);
  position: sticky;
  bottom: 0;
  z-index: -1;
  background-position: center ;
  background-repeat: no-repeat;
  background-size: cover;
}
/*#endregion */
